"use client";

export default function ForecastSummary({ data }) {
  // -------------------------------
  // GUARD: No usable forecast data
  // -------------------------------
  if (!Array.isArray(data) || data.length === 0) {
    return (
      <div className="bg-white shadow rounded-lg p-4 mt-4">
        <p className="text-gray-500 text-center">
          Forecast not available yet. Please retrain the model.
        </p>
      </div>
    );
  }

  // -------------------------------
  // SORT & FILTER VALID VALUES
  // -------------------------------
  const sorted = [...data]
    .filter(d => typeof d.value === "number" && !isNaN(d.value))
    .sort((a, b) => a.year - b.year);

  if (sorted.length === 0) {
    return (
      <div className="bg-white shadow rounded-lg p-4 mt-4">
        <p className="text-gray-500 text-center">
          Forecast not available yet. Please retrain the model.
        </p>
      </div>
    );
  }

  // -------------------------------
  // NEXT YEAR FORECAST
  // -------------------------------
  const nextYear = sorted[0];

  // -------------------------------
  // NEXT 2–5 YEAR AVERAGE
  // -------------------------------
  const firstFive = sorted.slice(0, 5);

  const avgForecast =
    firstFive.reduce((sum, entry) => sum + entry.value, 0) /
    firstFive.length;

  // -------------------------------
  // GROWTH RATE
  // -------------------------------
  const growthRate =
    firstFive.length > 1
      ? ((firstFive[firstFive.length - 1].value - firstFive[0].value) /
          firstFive[0].value) *
        100
      : 0;

  // -------------------------------
  // FORMATTER
  // -------------------------------
  const format = (v) =>
    new Intl.NumberFormat("en-PH", {
      style: "currency",
      currency: "PHP",
    }).format(v);

  // -------------------------------
  // RENDER
  // -------------------------------
  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4 my-4">
      <Card
        label={`Next Year Forecast (${nextYear.year})`}
        value={format(nextYear.value)}
        color="indigo"
      />

      <Card
        label="Next 2-Year Average Forecast"
        value={format(avgForecast)}
        color="green"
      />

      <Card
        label="Next Forecast Growth Rate"
        value={`${growthRate.toFixed(2)}%`}
        color="yellow"
      />
    </div>
  );
}

// -------------------------------
// CARD COMPONENT
// -------------------------------
function Card({ label, value, color }) {
  const colors = {
    indigo: "bg-indigo-50 text-indigo-700",
    green: "bg-green-50 text-green-700",
    yellow: "bg-yellow-50 text-yellow-700",
  };

  return (
    <div className={`p-4 rounded-lg text-center ${colors[color]}`}>
      <p className="text-sm text-gray-600">{label}</p>
      <p className="text-lg font-semibold">{value}</p>
    </div>
  );
}
