"use client"

import { useState, useEffect } from "react"
import { supabase } from "../../../lib/supabaseClient"
import { AlertCircle, TrendingUp, RefreshCw, Upload, BarChart3 } from "lucide-react"

// Hooks
import useBudgetData from "./lib/useBudgetData"
import useBreakdownData from "./lib/useBreakdownData"

// Components
import BudgetUploader from "./components/budgetUploader"
import SummaryCards from "./components/summaryCards"
//import IncomeTrendChart from "./components/incomeTrendChart"
import BreakdownPieSection from "./components/breakdownPieSection"
import ForecastChart from "./components/forecastChart"
import ForecastSummary from "./components/forecastSummary"

export default function TransactionPage() {
  const [session, setSession] = useState(null)
  const [selectedYear, setSelectedYear] = useState(null)
  const [forecastData, setForecastData] = useState([])
  const [isRetraining, setIsRetraining] = useState(false)
  const [error, setError] = useState(null)

  // Custom hooks
  const { budgetData, loading, fetchBudgetData } = useBudgetData()
  const { breakdownData, fetchBreakdownData } = useBreakdownData()

  useEffect(() => {
    if (budgetData.length > 0) {
      loadForecast();
    }
  }, [budgetData]);

  // -------------------------------------------------------
  // LOAD FORECAST FROM PYTHON BACKEND
  // -------------------------------------------------------
  async function loadForecast() {
    try {
      setError(null)
      const response = await fetch("/api/forecast", { cache: "no-store" });
      
      if (!response.ok) {
        throw new Error('Failed to fetch forecast data')
      }
      
      const data = await response.json();

      // Find last actual year
      const lastActualYear =
        budgetData.length > 0
          ? Math.max(...budgetData.map(d => d.year))
          : 0;

      // Keep only FUTURE forecast years
      const formatted = Array.isArray(data)
        ? data
            .filter(d => d.year > lastActualYear)
            .map(d => ({
              year: d.year,
              value: Number(d.yhat_upper),
              lower: Number(d.yhat_lower),
              upper: Number(d.yhat_upper),
            }))
        : [];

      console.log("Filtered future forecast:", formatted);
      setForecastData(formatted);

    } catch (err) {
      console.error("Forecast API error:", err);
      setError("Failed to load forecast data. Please try again.")
    }
  }

  // -------------------------------------------------------
  // RETRAIN MODEL
  // -------------------------------------------------------
  async function retrainModel() {
    try {
      setIsRetraining(true)
      setError(null)
      console.log("Retraining Prophet model...");

      const res = await fetch("/api/retrain", {
        method: "POST",
      });

      if (!res.ok) {
        throw new Error('Failed to retrain model')
      }

      const data = await res.json();
      console.log("Retrain response:", data);

      await loadForecast();
    } catch (err) {
      console.error("Retrain error:", err);
      setError("Failed to retrain model. Please try again.")
    } finally {
      setIsRetraining(false)
    }
  }

  // -------------------------------------------------------
  // AUTH CHECK
  // -------------------------------------------------------
  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session)
      if (session) {
        fetchBudgetData()
        fetchBreakdownData()
      }
    })

    const { data: listener } = supabase.auth.onAuthStateChange(
      (_event, session) => {
        setSession(session)
        if (session) {
          fetchBudgetData()
          fetchBreakdownData()
        }
      }
    )

    return () => listener.subscription.unsubscribe()
  }, [])

  // -------------------------------------------------------
  // WHEN BUDGET DATA IS READY, RETRAIN MODEL ONCE
  // -------------------------------------------------------
  useEffect(() => {
    if (budgetData.length > 0) {
      console.log("Budget data ready — retraining model once.")
      retrainModel()
    }
  }, [budgetData])

  // -------------------------------------------------------
  // RENDER UI
  // -------------------------------------------------------
  if (!session) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="max-w-md w-full mx-auto">
          <div className="bg-white shadow-lg rounded-lg p-8 text-center">
            <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-blue-100 mb-4">
              <AlertCircle className="h-6 w-6 text-blue-600" />
            </div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">Authentication Required</h3>
            <p className="text-sm text-gray-600">Please log in to access the budget dashboard</p>
          </div>
        </div>
      </div>
    )
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <RefreshCw className="h-8 w-8 animate-spin text-blue-600 mx-auto mb-4" />
          <h3 className="text-lg font-medium text-gray-900 mb-2">Loading Dashboard</h3>
          <p className="text-sm text-gray-600">Fetching your budget data...</p>
        </div>
      </div>
    )
  }

  const selectedData = breakdownData[selectedYear] || {}

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header Section */}
      <div className="bg-white shadow-sm border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="flex items-center justify-center h-10 w-10 rounded-lg bg-blue-600">
                <BarChart3 className="h-6 w-6 text-white" />
              </div>
              <div>
                <h1 className="text-2xl font-bold text-gray-900">Barangay Budget Dashboard</h1>
                <p className="text-sm text-gray-600">Monitor and forecast your budget performance</p>
              </div>
            </div>
            {budgetData.length > 0 && (
              <div className="flex items-center space-x-2 text-sm text-gray-600">
                <TrendingUp className="h-4 w-4" />
                <span>{budgetData.length} years of data</span>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Error Message */}
        {error && (
          <div className="mb-6 bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex items-center">
              <AlertCircle className="h-5 w-5 text-red-400 mr-3" />
              <div className="text-sm text-red-800">{error}</div>
            </div>
          </div>
        )}

        <div className="space-y-8">
          {/* Upload Section */}
          <div className="bg-white rounded-lg shadow-sm border border-gray-200">
            <div className="px-6 py-4 border-b border-gray-200">
              <div className="flex items-center space-x-2">
                <Upload className="h-5 w-5 text-gray-500" />
                <h3 className="text-lg font-medium text-gray-900">Data Management</h3>
              </div>
              <p className="mt-1 text-sm text-gray-600">Upload new budget data to update your dashboard</p>
            </div>
            <div className="p-6">
              <BudgetUploader refresh={fetchBudgetData} />
            </div>
          </div>

          {/* Summary Cards */}
          {budgetData.length > 0 && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200">
              <div className="px-6 py-4 border-b border-gray-200">
                <h3 className="text-lg font-medium text-gray-900">Budget Overview</h3>
                <p className="mt-1 text-sm text-gray-600">Key financial metrics and performance indicators</p>
              </div>
              <div className="p-6">
                <SummaryCards data={budgetData} />
              </div>
            </div>
          )}

          {/* Income Trend Chart */}
          {budgetData.length > 0 && (
            <div className="bg-white rounded-lg shadow-sm border border-gray-200">
              <div className="px-6 py-4 border-b border-gray-200">
                <h3 className="text-lg font-medium text-gray-900">Historical Trends</h3>
                <p className="mt-1 text-sm text-gray-600">Track your budget performance over time</p>
              </div>
              <div className="p-6">
                <IncomeTrendChart data={budgetData} onYearClick={setSelectedYear} />
              </div>
            </div>
          )}

          {/* Forecast Section */}
          {forecastData.length > 0 && (
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
              {/* Forecast Summary */}
              <div className="lg:col-span-1">
                <div className="bg-white rounded-lg shadow-sm border border-gray-200">
                  <div className="px-6 py-4 border-b border-gray-200">
                    <h3 className="text-lg font-medium text-gray-900">Forecast Summary</h3>
                    <p className="mt-1 text-sm text-gray-600">Predicted budget performance</p>
                  </div>
                  <div className="p-6">
                    <ForecastSummary data={forecastData} />
                  </div>
                </div>
              </div>

              {/* Forecast Chart */}
              <div className="lg:col-span-2">
                <div className="bg-white rounded-lg shadow-sm border border-gray-200">
                  <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between">
                    <div>
                      <h3 className="text-lg font-medium text-gray-900">Budget Forecast</h3>
                      <p className="mt-1 text-sm text-gray-600">AI-powered budget predictions</p>
                    </div>
                    <button
                      onClick={retrainModel}
                      disabled={isRetraining}
                      className="inline-flex items-center px-3 py-2 border border-gray-300 shadow-sm text-sm leading-4 font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50"
                    >
                      <RefreshCw className={`h-4 w-4 mr-2 ${isRetraining ? 'animate-spin' : ''}`} />
                      {isRetraining ? 'Retraining...' : 'Retrain Model'}
                    </button>
                  </div>
                  <div className="p-6">
                    <ForecastChart
                      historyData={budgetData}
                      forecastData={forecastData}
                      retrainModel={retrainModel}
                    />
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Empty State */}
          {budgetData.length === 0 && !loading && (
            <div className="text-center py-12">
              <div className="mx-auto flex items-center justify-center h-12 w-12 rounded-full bg-gray-100 mb-4">
                <BarChart3 className="h-6 w-6 text-gray-400" />
              </div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">No Budget Data Found</h3>
              <p className="text-sm text-gray-600 mb-6">Get started by uploading your first budget file</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}