"use client";

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
  ResponsiveContainer,
  Legend
} from "recharts";

export default function ForecastChart({ historyData, forecastData, retrainModel }) {

  // ---------- ACTUAL DATA ----------
  const safeHistory = Array.isArray(historyData)
    ? historyData.map(d => ({
        year: d.year,
        actual: d.totalIncome ?? 0,
      }))
    : [];

  // ---------- FORECAST DATA (PROPHET) ----------
  const safeForecast = Array.isArray(forecastData)
  ? forecastData.map(f => ({
      year: f.year,
      forecast: f.value ?? 0,
    }))
  : [];


  const combinedData = [...safeHistory, ...safeForecast];

  return (
    <div className="bg-white shadow rounded-lg p-4 mt-6">
      <h3 className="text-lg font-semibold mb-2">
        Actual vs Forecasted Income (Next 5 Years)
      </h3>

      <ResponsiveContainer width="100%" height={350}>
        <LineChart data={combinedData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="year" />
          <YAxis tickFormatter={(v) => `₱${(v / 1_000_000).toFixed(1)}M`} />
          <Tooltip formatter={(v) => `₱${v.toLocaleString()}`} />
          <Legend />

          <Line
            type="monotone"
            dataKey="actual"
            name="Actual Income"
            stroke="#4F46E5"
            strokeWidth={3}
            dot={{ r: 4 }}
            isAnimationActive={false}
            connectNulls
          />

          <Line
            type="monotone"
            dataKey="forecast"
            name="Forecast Income (Prophet)"
            stroke="#10B981"
            strokeDasharray="6 6"
            strokeWidth={3}
            dot={{ r: 4 }}
            isAnimationActive={false}
            connectNulls
          />
        </LineChart>
      </ResponsiveContainer>

      <button
        onClick={retrainModel}
        className="mt-4 px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700"
      >
        Retrain Forecast Model
      </button>
    </div>
  );
}
