"use client"

import Papa from "papaparse"
import * as XLSX from "xlsx"
import { supabase } from "../../../../lib/supabaseClient"
import { useEffect, useState } from "react"

export default function BudgetUploader({ refresh }) {
  const [uploading, setUploading] = useState(false)
  const [adding, setAdding] = useState(false)
  const [isOpen, setIsOpen] = useState(false)
  const [budgets, setBudgets] = useState([])

  const [year, setYear] = useState("")
  const [amount, setAmount] = useState("")
  const [forecastYears, setForecastYears] = useState(3) // default 5 years

  // =============================
  // LOAD EXISTING BUDGETS
  // =============================
  async function loadBudgets() {
    const { data, error } = await supabase
      .from("yearly_budget")
      .select("*")
      .order("year", { ascending: true })

    if (!error) setBudgets(data)
  }

  useEffect(() => {
    loadBudgets()
  }, [])

  // =============================
  // DELETE ONE YEAR
  // =============================
  async function deleteBudget(y) {
    if (!confirm(`Delete budget for year ${y}?`)) return

    await supabase.from("yearly_budget").delete().eq("year", y)
    loadBudgets()
    refresh()
  }

  // =============================
  // FILE UPLOAD
  // =============================
  async function handleFileUpload(event) {
    const file = event.target.files[0]
    if (!file) return
    setUploading(true)

    try {
      let rows = []
      const fileName = file.name.toLowerCase()

      if (fileName.endsWith(".csv")) {
        const text = await file.text()
        rows = Papa.parse(text, { header: true }).data
      } else {
        const buffer = await file.arrayBuffer()
        const sheet = XLSX.read(buffer).Sheets["Sheet1"]
        rows = XLSX.utils.sheet_to_json(sheet)
      }

      for (const row of rows) {
        const year = Number(row.year)
        const income = Number(String(row.total_income).replace(/[^\d.-]/g, ""))
        if (!year || !income) continue

        await supabase.from("yearly_budget").upsert({
          year,
          total_income: income
        })
      }

      await loadBudgets()
      refresh()
      alert("Upload successful!")
    } catch (e) {
      console.error(e)
      alert("Upload error.")
    }

    setUploading(false)
  }

  // =============================
  // MANUAL ADD FORM
  // =============================
  async function handleAddBudget(e) {
    e.preventDefault()
    if (!year || !amount) {
      alert("Please fill out both fields.")
      return
    }

    setAdding(true)

    try {
      await supabase.from("yearly_budget").upsert({
        year: Number(year),
        total_income: Number(amount),
      })

      setYear("")
      setAmount("")
      await loadBudgets()
      refresh()
      alert("Budget added!")
    } catch (err) {
      console.error(err)
      alert("Error adding budget.")
    }

    setAdding(false)
  }

  // =============================
  // RENDER
  // =============================
  return (
    <div className="mb-8 space-y-6">

      {/* ========== BUTTONS ========== */}
      <div className="flex items-center gap-4">
        {/* UPLOAD BUTTON */}
        <label className="inline-block bg-blue-600 text-white px-4 py-2 rounded-lg cursor-pointer hover:bg-blue-700">
          {uploading ? "Uploading..." : "Upload CSV/Excel"}
          <input type="file" className="hidden" onChange={handleFileUpload} />
        </label>

        {/* ADD NEW BUDGET (COLLAPSE) */}
        <button
          onClick={() => setIsOpen(!isOpen)}
          className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700"
        >
          {isOpen ? "Hide Manual Entry" : "Add New Budget"}
        </button>

        {/* FORECAST YEARS SELECTOR */}
        <select
          value={forecastYears}
          onChange={(e) => setForecastYears(Number(e.target.value))}
          className="border px-3 py-2 rounded-lg"
        >
          <option value={3}>Forecast: 1 Year</option>
          <option value={5}>Forecast: 2 Years</option>
          <option value={3}>Forecast: 3 Years</option>
          <option value={5}>Forecast: 4 Years</option>
          <option value={5}>Forecast: 5 Years</option>

         

        </select>
      </div>

      {/* ========== COLLAPSIBLE MANUAL ENTRY ========== */}
      <div
        className={`overflow-hidden transition-all duration-300 ${
          isOpen ? "max-h-[500px] opacity-100" : "max-h-0 opacity-0"
        }`}
      >
        <form
          onSubmit={handleAddBudget}
          className="bg-white shadow p-4 rounded-lg space-y-3 max-w-md mt-3"
        >
          <h3 className="text-lg font-semibold">Add New Budget</h3>

          <div>
            <label className="block text-sm font-medium">Year</label>
            <input
              type="number"
              min="2000"
              max="2100"
              value={year}
              onChange={(e) => setYear(e.target.value)}
              className="mt-1 block w-full border rounded-lg p-2"
              placeholder="Enter year (e.g., 2025)"
            />
          </div>

          <div>
            <label className="block text-sm font-medium">Total Income</label>
            <input
              type="number"
              value={amount}
              onChange={(e) => setAmount(e.target.value)}
              className="mt-1 block w-full border rounded-lg p-2"
              placeholder="Enter amount (e.g., 2500000)"
            />
          </div>

          <button
            type="submit"
            className="w-full bg-green-600 text-white py-2 rounded-lg hover:bg-green-700"
          >
            {adding ? "Saving..." : "Add Budget"}
          </button>
        </form>
      </div>

      {/* ========== TABLE OF BUDGETS (DELETE SUPPORT) ========== */}
      <div className="bg-white shadow p-4 rounded-lg">
        <h3 className="text-lg font-semibold mb-3">Budgets</h3>

        <table className="w-full text-sm">
          <thead>
            <tr className="text-left border-b">
              <th className="py-2">Year</th>
              <th>Total Income</th>
              <th></th>
            </tr>
          </thead>

          <tbody>
            {budgets.map((b) => (
              <tr key={b.year} className="border-b">
                <td className="py-2">{b.year}</td>
                <td>₱{b.total_income.toLocaleString()}</td>
                <td className="text-right">
                  <button
                    onClick={() => deleteBudget(b.year)}
                    className="text-red-600 hover:underline"
                  >
                    Delete
                  </button>
                </td>
              </tr>
            ))}

            {budgets.length === 0 && (
              <tr>
                <td colSpan={3} className="py-3 text-center text-gray-500">
                  No data yet.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

    </div>
  )
}
